body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'Rajdhani', sans-serif; }

h1,
h2,
h3,
h4,
h5 {
  font-family: 'Noto Serif KR', serif;
  margin: 0; }

p {
  margin: 0; }

h1 {
  font-size: 25px;
  margin-top: 0;
  color: #636161;
  letter-spacing: 1px;
  text-align: center; }
  @media (min-width: 768px) {
    h1 {
      font-size: 28px; } }
